.container {
    display: flex;
    flex-direction: column;
    margin:  auto;
    width: 420px;
    margin-top: 100px;
    margin-bottom: 100px;
    background: white;
    border-radius: 20px;  
}

.header {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 9px;
    width: 100%;
    margin-top: 30px;    
}

.text  {
    color: #3c009d;
    font-size: 40px;
    font-weight: 600;
}

.welcome-text {
    color: black;
    text-align: center;
    font-size: 13px;
    margin-top: 10px;
}

.underline {
    width: 65px;
    height: 6px;
    background: #3c009d;
    border-radius: 9px;
}

.inputs {
    margin-top: 55px;
    display: flex;
    flex-direction: column;
    gap: 25px;
}

.input {
    display: flex;
    align-items: center;
    margin: auto;
    width: 380px;
    height: 55px;
    background: #eaeaea;
    border-radius: 20px;
}

.input img {
    margin: 0px 30px;
}

.input input {
    height: 50px;
    width: 300px;
    background: transparent;
    border: none;
    outline: none;
    color: #797979;
    margin-left: 10px;
    font-size: 15px;
}

.forgot-password {
    padding-left: 25px;
    color: #797979;
    font-size: 18px;    
}

.forgot-password span {
    color: #4c00b4;
    cursor: pointer;
}

.submit-container {
    display: flex;
    gap: 30px;
    margin: 60px auto;
}

.submit {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 175px;
    height: 50px;
    color: #fff;
    background: #4c00b4;
    border-radius: 50px;
    font-size: 19px;
    font-weight: 700;
    cursor: pointer;
}

.gray {
    background: #eaeaea;
    color: #676767;
}