.home {
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 16px;
    width: 100%;
    margin: 0;
    
    box-sizing: border-box;
    padding: 30px;
}
.home-display {
    background: white;
    font-size: 25px;
    font-weight: 600;
    width: 100%;  
    border-radius: 20px;
    height: 40px;
    text-align: center;   
}

.home-display p {
    animation: animate 1.5s linear infinite;
}

@keyframes animate{ 
    0%{ 
      opacity: 0; 
    } 
    50%{ 
      opacity: 0.7; 
    } 
    100%{ 
      opacity: 0; 
    } 
  }

  .home-bar {
    display: flex;
    width: 100%;
    background: blueviolet;
    justify-content: space-around;
    margin-top: 10px;
    border-radius: 20px;
    height: 127px;
  }

  .home-left, .home-right {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 10px; 
    margin-top: 7px;
    
  }

  .home-bar h1 {
    color: white;
    font-size: 20px;
    font-weight: 600;
  }

  .home-bar h2 {
    color: white;
    font-size: 12PX;
    font-weight: 600;
  }

  .home-bar button {
    width: 130px;
    height: 40px;
    border-radius: 75px;
    color: #515151;
    font-size: 15px;
    font-weight: 600;
    background-color: white;
    cursor: pointer;
    
  }

  .products-tittle{
       display: flex;
       flex-direction: column;
       align-items: center;
       text-align: center;  
       font-size: 10px;
       width: 100%;
       border-radius: 20px;
       margin-top: 10px;
  }

  .Products-bar {
    display: flex;
    flex-direction: row;
    color: white;
    gap: 20px;
    margin: auto;
    margin-bottom: 15px;
  }
  .products-tittle p {
    color: white;
    margin: auto;
    margin-top: 30px;
    font-weight: 600;
    font-size: 20px;
  }

  .products-tittle img {
    width: 30%;
  }

  .products-tittle h1 {
    color: blueviolet;
    font-size: 10px;
  }
  .products-tittle h2 {
    color: white;
    font-size: 12px;
  }


  .product-left {
    display: flex;
    flex-direction: column;
    text-align: center;
    justify-content: space-around;
  }


  .product-middle {
    display: flex;
    flex-direction: column;
    text-align: center;
    justify-content: space-around;
  }

  .product-right {
    display: flex;
    flex-direction: column;
    text-align: center;
    justify-content: space-around;
  }

  .products-tittle button {
    width: 300px;
    height: 35px;
    background: blueviolet;
    border-radius: 50px;
    color: white;
    font-size: 15px;
    font-weight: 600;
    cursor: pointer;
    border-style: none;
    margin-bottom: 15px;
  }

  .products-tittle hr {
    border: none;
    width: 80%;
    height: 3px;
    border-radius: 10px;
    background-color: white;
    margin-top: 20px;
    margin-bottom: 20px;
  }

  .menu-items {
    display: flex;
    align-items: center;
    justify-content: center;
    background: blueviolet;
    width: 100%;
    height: 70px;
    border-radius: 10px;
       
  }


  .nav-menu {
    display: flex;
    justify-content: center;
    align-items: center;
    list-style: none;
    gap: 10px;
    color: white;
    
}

.nav-menu p {
    margin: 0;
    color: white;
    text-decoration: none;
    font-size: 150%;
    font-weight: 600;
}

.nav-menu li {
    display: flex;
    flex-direction: column;
    align-items: center;
    cursor: pointer;
    justify-content: center;
     
}

.nav-menu img {
    border-radius: 20%;
}




  
 
  
  
  
 

  









